<template>
  <v-layout v-align="center">
    <v-card>
      <v-layout v-flex="50" v-align="center" v-column>

      <v-toolbar>
        <v-toolbar-title>Login to {{ title }}</v-toolbar-title>
      </v-toolbar>
      <v-card-text>
        <form novalidate class="login-screen"
              v-on:submit.prevent="submit">
        <v-container fluid grid-list-xl>
        <v-layout  row wrap>
        <v-flex class="xs12">
            <v-text-field
              v-model="email"
              label="email"
              :error-messages="errors.collect('email')"
              v-validate="'email'"
              data-vv-name="email"
            ></v-text-field>
              <v-text-field
                hint="At least 6 characters"
                v-model="rawPassword"
                :append-icon="passwordHidden ? 'visibility' : 'visibility_off'"
                :append-icon-cb="() => (passwordHidden = !passwordHidden)"
                :type="passwordHidden ? 'password' : 'text'"
                counter
                label="password"
                :error-messages="errors.collect('password')"
                v-validate="'required|min:6'"
                data-vv-name="password"
            ></v-text-field> 
            <p><a href="/forgotPassword">Forgot</a> your password?   </p>
              <div class="alert alert-danger">{{loginMessage}}</div>
          </v-flex>
        </v-layout>
        <v-btn type="submit" class="v-accent" >Sign in</v-btn>
        <v-btn type="button" class="v-primary" @click="clearForm">Cancel</v-btn>

          <div v-if="error" style="color: red">
            {{ error }}
          </div>

          <div style="margin-top: 3em">
            New to {{ title }}? &nbsp;
            <router-link to="/register">Register</router-link>
          </div>
</v-container>
        </form>
</v-card-text>
      </v-layout>
    </v-card>
  </v-layout>
</template>

<script>
import auth from '../modules/auth'
import config from '../config'

export default {
  name: 'Login',
  data () {
    return {
      title: config.title,
      email: '',
      rawPassword: '',
      error: '',
      passwordHidden:true
    }
  },
  methods: {
    async submit () {
      let payload = {
        email: this.$data.email,
        rawPassword: this.$data.rawPassword
      }
      console.log('> Login.submit', payload)
      let response = await auth.login(payload)
      console.log('> Login.submit response', response)

      if (response.result) {
        this.$router.push('/')
      } else {
        this.error = response.error.message
      }
    }
  }
}
</script>



