<template>
  <v-toolbar>
    <h2
      class="v-title"
      style="
        padding-left: 1em;
        cursor: pointer;
        flex: 1"
      @click="home()">
      {{ title }}
    </h2>
    <v-tabs dark>
      <v-tab id="tab-home" to="/home" router>Home
      </v-tab>
      <v-tab id="tab-search" to="/search" router>Search
      </v-tab>
      <v-tab id="tab-view" to="/view" router>View
      </v-tab>
    </v-tabs>
    <div v-if="isUser">

      <v-menu v-if="user.authenticated">

        <v-btn v-menu-trigger>
          {{user.name}}
        </v-btn>

        <v-menu-content>

          <v-menu-item @click="editUser">
            Edit User
          </v-menu-item>

          <v-menu-item @click="logout">
            Logout
          </v-menu-item>

        </v-menu-content>

      </v-menu>

      <router-link
        v-else
        to='/login'
        tag='v-btn'>
        Login
      </router-link>

    </div>

  </v-toolbar>
</template>

<script>
import auth from "../modules/auth";
import config from "../config";

export default {
  name: "navbar",
  data() {
    return {
      title: config.title,
      isUser: config.isUser
    };
  },
  computed: {
    user: function() {
      return this.$store.state.user;
    }
  },
  methods: {
    editUser() {
      this.$router.push("/edit-user");
    },
    home() {
      this.$router.push("/");
    },
    async logout() {
      await auth.logout();
      this.$router.push("/login");
    }
  }
};
</script>
<style scoped>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 0px;
}
</style>


